<script>
import Card from "./Card.svelte"
export let doc
export let open

let color = doc.type === "base" ? "green" : doc.type === "advanced" ? "darkgreen" : "blue"
</script>

<Card size="small" bind:heading={doc.name} bind:open={open} color="{color}">
  {#if doc.type === "base"}
    <p>Automatically gained at level 1</p>
  {/if}
  {#if doc.type === "advanced"}
    <p>Automatically gained at level 6</p>
  {/if}
  <p>{doc.description}</p>
</Card>
