import { combineLatest, BehaviorSubject } from "rxjs"
import { map, tap, switchMap } from "rxjs/operators"
import { fromFetch } from 'rxjs/fetch'
import yaml from "js-yaml"


const arcana = new BehaviorSubject([])
const archetypes = new BehaviorSubject([])
const basicActions = new BehaviorSubject([])
const characterClasses = new BehaviorSubject([])
const disciplines = new BehaviorSubject([])

arcana.subscribe(fromFetch("./data/arcana.yaml"))
disciplines.subscribe(fromFetch("./data/disciplines.yaml"))
basicActions.subscribe(fromFetch("./data/basic-actions.yaml"))
fromFetch("./data/arcana.yaml").pipe(
  switchMap(response => response.text()),
  map(text => yaml.load(text))
).subscribe(arcana)

fromFetch("./data/archetypes.yaml").pipe(
  switchMap(response => response.text()),
  map(text => yaml.load(text))
).subscribe(archetypes)

fromFetch("./data/basic-actions.yaml").pipe(
  switchMap(response => response.text()),
  map(text => yaml.load(text))
).subscribe(basicActions)

fromFetch("./data/disciplines.yaml").pipe(
  switchMap(response => response.text()),
  map(text => yaml.load(text))
).subscribe(disciplines)

combineLatest([fromFetch("./data/character-classes.yaml").pipe(
  switchMap(response => response.text()),
  map(text => yaml.load(text))
), archetypes]).pipe(
  map(([classes, arches]) => {
    const archetypeLookup = {}
    arches.forEach(arch => {
      archetypeLookup[arch.name] = arch
    })
    return classes.map(cls => {
      const archFeatures = cls.archetypes.map(arch => archetypeLookup[arch].features)

      console.log(archFeatures)
      return {
        ...cls,
        features: [].concat(cls.features, ...archFeatures)
      }
    })
  })
).subscribe(characterClasses)


export {
  arcana,
  archetypes,
  basicActions,
  disciplines,
  characterClasses
}
